<template>
  <div class="newSongList">
    <li class="newSongItem" @click="$emit('current-song', songInfo)">
      <!-- <div class="img" v-if="currentSong && newSongItem.id == currentSong.id">
        <img src="../assets/sing.png" alt />
      </div> -->

      <span
        class="order"
        v-if="songInfo.order !== undefined && songInfo.isShowInfo == true"
      >{{songInfo.order+1}}</span>
      <div
        class="center"
        :style="{marginLeft: songInfo.order !== undefined && songInfo.isShowInfo == true? '0px':'10px'}"
      >
        <h3>
          {{newSongItem.name}}
          <span
            v-if="songInfo.order !== undefined"
          >{{songInfo.order !== undefined || songInfo.isSearch !== undefined ?'':newSongItem.song.alias[0]}}</span>
        </h3>
        <div class="info" v-if="songInfo.isShowInfo">
          <i class="icon icon-quality"></i>
          <span
            class="artists"
          >{{songInfo.order !== undefined ? joinName(newSongItem.ar) : songInfo.isSearch !== undefined ? joinName(newSongItem.artists) : joinName(newSongItem.song.artists)}}</span>

          <span
            class="album"
          >-{{songInfo.order !== undefined ? newSongItem.al.name : songInfo.isSearch !== undefined ? newSongItem.album.name : newSongItem.song.album.name}}</span>
        </div>
      </div>
      <template v-if="songInfo.isShowInfo">
        <i class="icon icon-play"></i>
      </template>

      <template v-else>
        <van-icon
          @click.stop="$emit('del-currPL',newSongItem)"
          name="cross"
          color="#cdcdcd"
          size="18"
          class="icon icon-play"
          style="background:none"
        />
      </template>
    </li>
  </div>
</template>

<script>
export default {
  name: "NewSongList",
  props: {
    newSongItem: Object,
    songInfo: Object
  },
  data() {
    return {
      currentSong: this.songInfo.newSongItem
    };
  },

  methods: {
    joinName(artists) {
      return artists.map(artist => artist.name).join("/");
    },
    delCurrentPlayList(e) {
      // console.log("e==>", e);

      var cacheCurrentPlayList = JSON.parse(
        window.localStorage.getItem("currentPlayList")
      );

      for (let i = 0; i < cacheCurrentPlayList.length; i++) {
        if (cacheCurrentPlayList[i].newSongItem.id == e.id) {
          cacheCurrentPlayList.splice(i, 1);
        }
      }

      window.localStorage.setItem(
        "currentPlayList",
        JSON.stringify(cacheCurrentPlayList)
      );
    }
  }
};
</script>

<style lang="less" scoped>
li {
  border-bottom: 1px solid #f5f5f5;
}
.img {
  width: 24px;
  height: 24px;
}
.delete {
  display: inline-block;
}
.icon {
  background-image: url("../assets/index_icon_2x.png");
  display: inline-block;
  background-size: 166px auto;
  &.icon-quality {
    width: 12px;
    height: 8px;
    margin: 0 5px 0 2px;
  }
  &.icon-play {
    width: 25px;
    height: 25px;
    background-position-x: -23px;
    margin: 0 10px;
  }
}
.newSongItem {
  display: flex;
  margin: 5px 0;
  align-items: center;
  .order {
    font-size: 18px;
    width: 2em;
    text-align: center;
    color: rgb(189, 189, 189);
  }
  .center {
    flex: 1;
    width: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-left: 10px;
    h3 {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      font-size: 15px;
      .alias {
        color: rgb(128, 104, 104);
      }
    }
    .info {
      font-size: 12px;
      line-height: 2em;
      color: rgb(189, 189, 189);
    }
  }
}
</style>