import Vue from "vue";
import "./plugins/axios";
import App from "./App.vue";
import router from "./router";
import 'lib-flexible';
Vue.config.productionTip = false;

import {
  Tab,
  Tabs,
  Icon,
  Toast,
  ActionSheet,
  Search
} from 'vant';

Vue.use(Tab)
  .use(Tabs)
  .use(Icon)
  .use(Toast)
  .use(ActionSheet)
  .use(Search)

new Vue({
  router,
  render: h => h(App)
}).$mount("#app");