
<template>
  <div class="playbar">
    <div class="playbarbottom" @click="showFullscreen=true">
      <img
        class="thumb"
        :class="{active: playing}"
        :src="songInfo.order !== undefined ? currentSong.al.picUrl : songInfo.isSearch !== undefined ? currentSong.album.artist.img1v1Url : currentSong.picUrl"
        alt
      />
      <div class="info">
        <h5>{{currentSong.name}}</h5>
        <span>{{songInfo.order !== undefined ? joinName(currentSong.ar): songInfo.isSearch !== undefined ? joinName(currentSong.artists) : joinName(currentSong.song.artists)}}</span>
      </div>
      <div class="progress" @click.stop="playing = !playing">
        <canvas width="36" height="36" id="canvas"></canvas>
        <i v-if="playing" class="fa fa-pause"></i>
        <i v-else class="fa fa-play"></i>
      </div>
      <div class="playlist" @click.stop="show=true"></div>
    </div>

    <div class="showList">
      <van-action-sheet v-model="show" title="当前播放" @click="show=false">
        <ul class="content">
          <NewSongList
            v-for="(newSong,index) in currentPlayList"
            :key="index"
            :newSongItem="newSong.newSongItem"
            :currentSong="songInfo.newSongItem"
            :songInfo="{isShowInfo:false,newSongItem:newSong.newSongItem,order:currentPlayList[index].order,isSearch:currentPlayList[index].isSearch}"
            @current-song="$emit('current-song', $event)"
            @del-currPL="delCurrentPlayList($event)"
          ></NewSongList>
        </ul>
      </van-action-sheet>
    </div>

    <transition
      name="custom-classes-transition"
      enter-active-class="animated fadeIn"
      leave-active-class="animated fadeOut"
    >
      <div v-show="showFullscreen" class="fullscreenplay">
        <div
          class="mask"
          :style="{backgroundImage: `url(${songInfo.order !== undefined ? currentSong.al.picUrl : songInfo.isSearch !== undefined ? currentSong.album.artist.img1v1Url : currentSong.picUrl})`}"
        ></div>
        <header>
          <van-icon
            name="arrow-left"
            color="#ffffff"
            size="20"
            class="icon"
            @click="showFullscreen=false"
          />
          <div class="bigInfo">
            <div class="songName">{{currentSong.name}}</div>
            <div
              class="singer"
            >{{songInfo.order !== undefined ? joinName(currentSong.ar): songInfo.isSearch !== undefined ? joinName(currentSong.artists) : joinName(currentSong.song.artists)}}</div>
          </div>
        </header>

        <div
          v-show="showCircle"
          class="circle"
          :class="{active: playing}"
          @click="showCircle = false"
        >
          <div class="needle"></div>
          <div class="glue">
            <img
              :src="songInfo.order !== undefined ? currentSong.al.picUrl : songInfo.isSearch !== undefined ? currentSong.album.artist.img1v1Url : currentSong.picUrl"
              alt
            />
          </div>
        </div>
        <div v-show="!showCircle" class="lyric-box" @click="showCircle = true">
          <ul class="scroll" :style="{transform: `translateY(${-currentLyricIndex * 28}px)`}">
            <li
              v-for="(item, index) in parsedLyric"
              :class="{active: index===currentLyricIndex}"
              :key="index"
            >{{item.text}}</li>
          </ul>
        </div>

        <footer>
          <div class="top">
            <span class="currentTime">{{cuTime}}</span>
            <div class="bigprogress">
              <input type="range" />
            </div>

            <span class="currentTime">{{duTime}}</span>
          </div>

          <div class="bottom">
            <div class="playmode" @click="toggleMode" v-if="playmode==0">
              <img class="auto-img" src="../assets/order.png" />
            </div>
            <div class="playmode" @click="toggleMode" v-else-if="playmode==1">
              <img class="auto-img" src="../assets/random.png" />
            </div>
            <div class="playmode" @click="toggleMode" v-else>
              <img class="auto-img" src="../assets/onlyOne.png" />
            </div>

            <div class="playStyle">
              <div class="pre" @click="$emit('pre-click', currentSong)"></div>
              <div class="play-pause" :class="{active:playing}" @click="playing = !playing"></div>
              <div class="next" @click="$emit('next-click', currentSong)"></div>
            </div>
            <div class="list" @click="show=true"></div>
          </div>
        </footer>
      </div>
    </transition>

    <audio :src="currentSongUrl" controls autoplay></audio>
  </div>
</template>

<script>
import NewSongList from "@/components/NewSongList.vue";

export default {
  name: "PlayBar",
  components: { NewSongList },
  props: {
    currentSong: Object,
    songInfo: Object
  },
  data() {
    return {
      playing: false,
      show: false,
      showFullscreen: false,
      showCircle: false,
      currentLyric: null,
      currentLyricIndex: 0,
      currentPlayList: [this.songInfo],
      playmode: 0,
      duTime: "00:00",
      cuTime: "00:00",
      touchstartX: 0,
      touchstartY: 0,
      //滑块的left最大最小值
      minLeft: 0,
      maxLeft: 0,
      //进度条的最大最小值
      bminLeft: 0,
      bmaxLeft: 0
    };
  },
  computed: {
    currentSongUrl() {
      if (this.currentSong) {
        return `https://music.163.com/song/media/outer/url?id=${this.currentSong.id}.mp3`;
      } else {
        return "";
      }
    },
    parsedLyric: function() {
      if (this.currentLyric) {
        return this.currentLyric.split("\n").map(item => {
          // console.log(item);
          var regex = /\d{2}:\d{2}\.\d+/i;
          if (item.search(regex) !== -1) {
            var time = item.match(regex)[0];
            var m = time.substr(0, 2);
            var s = time.substr(3, 2);
            var n = time.substr(5);
            return {
              time: Number(m) * 60 + Number(s) + Number(n),
              text: item.substr(11) || "---------"
            };
          } else {
            return {};
          }
        });
      } else {
        return null;
      }
    }
  },
  methods: {
    // 切换播放模式
    toggleMode() {
      if (this.playmode == 2) {
        this.playmode = 0;
      } else {
        ++this.playmode;
      }
    },

    // 时间格式化
    formatTime(sec) {
      //sec: 秒数
      var minute = Math.floor(sec / 60);
      minute = minute >= 10 ? minute : "0" + minute;
      var second = Math.floor(sec % 60);
      second = second >= 10 ? second : "0" + second;
      return minute + ":" + second;
    },

    // 时间格式化 287150
    formatDuTime(sec) {
      //sec: 秒数
      var minute = Math.floor(sec / 60000);
      minute = minute >= 10 ? minute : "0" + minute;
      var second = Math.floor(sec % 60);
      second = second >= 10 ? second : "0" + second;
      return minute + ":" + second;
    },

    joinName(artists) {
      return artists.map(artist => artist.name).join("/");
    },
    drawProgress() {
      var canvas = this.$el.querySelector("#canvas");
      var audio = this.$el.querySelector("audio");
      var next = this.$el.querySelector(".next");
      // var active = this.$el.querySelector(".progress-active");
      var bigprogress = this.$el.querySelector(".bigprogress input");
      // var mask1 = this.$el.querySelector(".mask1");

      var duration =
        this.songInfo.order !== undefined
          ? this.currentSong.dt
          : this.songInfo.isSearch !== undefined
          ? this.currentSong.duration
          : this.currentSong.song.duration;

      audio.ontimeupdate = () => {
        /** @type {HTMLCanvasElement} */
        var duration =
          this.songInfo.order !== undefined
            ? this.currentSong.dt
            : this.songInfo.isSearch !== undefined
            ? this.currentSong.duration
            : this.currentSong.song.duration;

        // 获取canvas元素
        var context = canvas.getContext("2d");

        context.clearRect(0, 0, 36, 36);
        context.beginPath();
        context.arc(18, 18, 17, 0, 2 * Math.PI);
        context.closePath();
        context.strokeStyle = "gray";
        // 描边样式
        context.lineWidth = "1";
        // 描边宽度
        context.stroke(); // 路径描边
        context.beginPath();
        context.arc(
          18,
          18,
          17,
          -0.5 * Math.PI,
          -0.5 * Math.PI + 2 * Math.PI * ((this.currentTime * 1000) / duration)
        );
        context.strokeStyle = "rgba(255,0,0,0.7)";
        // 描边样式
        context.lineWidth = "1";
        // 描边
        context.stroke();

        // 滚动词
        // this.currentTime
        // currentLyricIndex
        let index;

        for (let i = 0; i < this.parsedLyric.length; i++) {
          if (audio.currentTime + 0.15 < this.parsedLyric[i].time) {
            index = i - 1;
            break;
          }
        }
        if (index === undefined) {
          index = this.parsedLyric.length - 1;
        }
        this.currentLyricIndex = index;

        //歌曲当前播放时间
        this.cuTime = this.formatTime(audio.currentTime);
        //歌曲总时间
        this.duTime = this.formatDuTime(duration);

        bigprogress.value = (audio.currentTime / (duration / 1000)) * 100;
      };

      bigprogress.onchange = function() {
        audio.currentTime = (this.value * duration) / 1000 / 100;
      };

      const cacheCurrentPlayList = JSON.parse(
        window.localStorage.getItem("currentPlayList")
      );
      audio.onended = () => {
        if (this.playmode == 0) {
          next.click();
        } else if (this.playmode == 1) {
          var randomIndex = Math.floor(
            Math.random() * cacheCurrentPlayList.length
          );
          this.$emit("random-playSong", randomIndex);
        } else {
          audio.load();
        }
      };
    },
    getLyric() {
      var cachedLyric = window.localStorage.getItem(
        "lyric" + this.currentSong.id
      );
      if (cachedLyric) {
        this.currentLyric = cachedLyric;
      } else {
        window.axios
          .get("lyric", {
            params: {
              id: this.currentSong.id
            }
          })
          .then(response => {
            this.currentLyric = response.data.lrc.lyric;
            window.localStorage.setItem(
              "lyric" + this.currentSong.id,
              response.data.lrc.lyric
            );
          });
      }
    },
    saveCurrentPlayList() {
      window.localStorage.setItem(
        "currentPlayList",
        JSON.stringify(this.currentPlayList)
      );
    },
    delCurrentPlayList(e) {
      var cacheCurrentPlayList = JSON.parse(
        window.localStorage.getItem("currentPlayList")
      );

      for (let i = 0; i < cacheCurrentPlayList.length; i++) {
        if (cacheCurrentPlayList[i].newSongItem.id == e.id) {
          cacheCurrentPlayList.splice(i, 1);
          this.currentPlayList = cacheCurrentPlayList;
        }
      }

      window.localStorage.setItem(
        "currentPlayList",
        JSON.stringify(cacheCurrentPlayList)
      );
    }
  },
  mounted() {
    var audio = this.$el.querySelector("audio");
    this.drawProgress();
    audio.onplay = () => {
      this.playing = true;
    };
    audio.onpause = () => {
      this.playing = false;
    };
  },
  watch: {
    playing(value) {
      var audio = this.$el.querySelector("audio");
      if (value) {
        audio.play();
      } else {
        audio.pause();
      }
    },

    currentSong(value) {
      this.getLyric();
      // 加入当前播放列表 过滤重复
      let isExist = this.currentPlayList.some(item => {
        return item.newSongItem.id == value.id;
      });
      if (!isExist) this.currentPlayList.unshift(this.songInfo);
      this.saveCurrentPlayList();
    }
  },
  created() {
    this.getLyric();

    //创建播放列表
    const cacheCurrentPlayList = JSON.parse(
      window.localStorage.getItem("currentPlayList")
    );

    if (cacheCurrentPlayList) {
      // 存在
      this.currentPlayList = cacheCurrentPlayList;
      let isExist = this.currentPlayList.some(item => {
        return item.newSongItem.id == this.currentSong.id;
      });
      if (!isExist) this.currentPlayList.unshift(this.songInfo);
      this.saveCurrentPlayList();
    } else {
      this.saveCurrentPlayList();
    }
  }
};
</script>

<style lang="less" scoped>
.content {
  padding: 16px 16px 160px;
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.playbarbottom {
  padding: 5px 0;
  background: white;
  border-top: 1px solid rgba(128, 128, 128, 0.2);
  display: flex;
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  .margin () {
    margin: 0 10px;
  }
  .thumb {
    .margin;
    width: 36px;
    height: 36px;
    border-radius: 50%;
    animation: rotate 6s linear infinite;
    animation-play-state: paused;
    &.active {
      animation-play-state: running;
    }
  }
  .info {
    flex: 1;
    font-size: 12px;
    line-height: 18px;
    span {
      color: gray;
    }
  }
  .progress {
    .margin;
    width: 28px;
    height: 28px;
    // background: lightgray;
    position: relative;
    #canvas {
      width: 100%;
    }
    i {
      position: absolute;
      top: 50%;
      left: 50%;
      color: lightgray;
      font-size: 12px;
      display: block;
      margin-top: -6px;
      margin-left: -6px;
      line-height: 1em;
      width: 12px;
      height: 12px;
      text-align: center;
    }
  }
  .playlist {
    .margin;
    width: 28px;
    height: 28px;
    // background-color: red;
    background-image: url(../assets/list.png);
    background-position: center center;
  }
}
.showList {
  position: relative;
  z-index: 9999;
}

.fullscreenplay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100vh;
  display: flex;
  background-color: #000;
  flex-direction: column;
  .mask {
    filter: blur(30px) brightness(0.5);
    transform: scale(2);
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    z-index: -1;
  }

  .circle {
    flex: 1;
    text-align: center;
    position: relative;
    .glue {
      padding: 54px;
      // background: red;
      background-image: url("../assets/disc_light-ip6.png"),
        url("../assets/disc-ip6.png");
      background-size: 100%;
      border-radius: 50%;
      display: inline-block;
      margin-top: 84px;
      animation: rotate 6s linear infinite;
      animation-play-state: paused;
      img {
        width: 180px;
        height: 180px;
        border-radius: 50%;
        vertical-align: middle;
      }
    }
    .needle {
      width: 96px;
      height: 137px;
      background-image: url("../assets/needle.png");
      background-size: 100%;
      position: absolute;
      top: 0;
      left: 50%;
      z-index: 1;
      margin-left: -16px;
      transform: rotate(-12deg);
      transform-origin: 16px 0;
      transition: all 0.3s;
    }
    &.active {
      .needle {
        transform: rotate(0deg);
      }
      .glue {
        animation-play-state: running;
      }
    }
  }
  .lyric-box {
    flex: 1;
    overflow: hidden;
    position: relative;
    .scroll {
      text-align: center;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 50%;
      margin-top: -1em;
      transition: transform 0.3s;
      li {
        line-height: 2em;
        font-size: 14px;
        height: 2em;
        color: gray;
        &.active {
          color: white;
        }
      }
    }
  }
}
.fadeIn {
  animation-duration: 0.3s;
}
header {
  height: 60px;
  color: #ffffff;
  display: flex;
  padding: 10px 5px 15px;
  // background: lightblue;
  .bigInfo {
    flex: 1;
  }
  .icon {
    display: inline-block;
    height: 30px;
    width: 30px;
    text-align: center;
    padding-top: 5px;
  }
  .singer {
    height: 20px;
    line-height: 20px;
  }
  .songName {
    height: 20px;
    line-height: 20px;
    font-size: 16px;
  }
}
footer {
  height: 100px;
  padding: 20px 0px;
  // background: lightblue;
  .all() {
    width: 40px;
    height: 40px;
    // background: red;
    display: inline-block;
    background-size: 24px 24px;
    background-position: center center;
    background-repeat: no-repeat;
  }
  .top {
    height: 15px;
    padding: 0 10px;
    // background: red;
    margin-bottom: 10px;
    color: #fff;
    .currentTime {
      margin-right: 5px;
      margin-top: 4px;
    }

    //去除系统默认的样式
    input[type="range"] {
      -webkit-appearance: none;
      width: 100%;
      border-radius: 10px; /*这个属性设置使填充进度条时的图形为圆角*/
    }
    input[type="range"]::-webkit-slider-thumb {
      -webkit-appearance: none;
    }

    //给滑动轨道(track)添加样式
    input[type="range"]::-webkit-slider-runnable-track {
      // height: 15px;
      // border-radius: 10px; /*将轨道设为圆角的*/
      // box-shadow: 0 1px 1px #def3f8, inset 0 0.125em 0.125em #0d1112; /*轨道内置阴影效果*/

      height: 3px;
      padding: 3px 0;
      position: relative;
      margin-top: -8px;
      // height: 3px;
      background-color: rgba(255, 255, 255, 0.5);
      // position: relative;
      z-index: 1;
    }
    input[type="range"]:focus {
      outline: none;
    }

    //给滑块(thumb)添加样式
    input[type="range"]::-webkit-slider-thumb {
      -webkit-appearance: none;
      position: absolute;
      left: 0;
      top: -2px;
      width: 10px;
      height: 10px;
      background-color: #fff;
      border-radius: 50%;
    }

    //根据滑块所在位置填充进度条
    input[type="range"]::-moz-progres-bar {
      // -wekbit-appearance: none;
      position: absolute;
      left: 0;
      top: 0;
      width: 0px;
      height: 3px;
      background: #fff;
    }

    .bigprogress {
      display: inline-block;
      width: 80%;
      margin-right: 5px;
    }

    // .progress-active {
    //   position: absolute;
    //   left: 0;
    //   top: 0;
    //   width: 0px;
    //   height: 3px;
    //   background: #fff;
    // }
  }
  .bottom {
    height: 40px;
    // background: pink;
    display: flex;
    padding: 0 40px;
  }
  .playmode {
    .all();
    // background-image: url(../assets/order.png);
    .auto-img {
      width: 24px;
      height: 24px;
      text-align: center;
      margin-top: 8px;
      margin-left: 8px;
    }
  }
  .playStyle {
    flex: 1;
    margin: 0 23px;
    .pre {
      .all();
      background-image: url(../assets/pre.png);
    }
    .play-pause {
      .all();
      margin: 0 23px;
      background-image: url(../assets/play.png);
      background-size: cover;
      &.active {
        background-image: url(../assets/pause.png);
      }
    }
    .next {
      .all();
      background-image: url(../assets/next.png);
    }
  }
  .list {
    .all();
    background-image: url(../assets/list.png);
  }
}
</style>