<template>
  <div id="app">
    <!-- 一级路由 -->
    <router-view @current-song="songInfo = $event"></router-view>
    <PlayBar
      v-if="songInfo.newSongItem"
      :songInfo="songInfo"
      :currentSong="songInfo.newSongItem"
      @current-song="songInfo = $event"
      @pre-click="preSong($event)"
      @next-click="nextSong($event)"
      @random-playSong="randomSong($event)"
    ></PlayBar>
  </div>
</template>
<script>
import PlayBar from "@/components/PlayBar.vue";

export default {
  name: "App",
  data() {
    return {
      songInfo: {
        currentSong: null,
        order: "",
        isSearch: "",
        isShowInfo: ""
      }
    };
  },
  components: { PlayBar },
  methods: {
    findCurrentSongidx(e) {
      var cacheCurrentPlayList = JSON.parse(
        window.localStorage.getItem("currentPlayList")
      );
      var index = "";
      for (let i = 0; i < cacheCurrentPlayList.length; i++) {
        if (cacheCurrentPlayList[i].newSongItem.id == e.id) {
          index = i;
          break;
        }
      }
      return index;
    },
    //下一首
    nextSong(id) {
      var cacheCurrentPlayList = JSON.parse(
        window.localStorage.getItem("currentPlayList")
      );
      var i =
        this.findCurrentSongidx(id) == cacheCurrentPlayList.length - 1
          ? 0
          : this.findCurrentSongidx(id) + 1;
      this.songInfo = cacheCurrentPlayList[i];
    },

    //上一首
    preSong(id) {
      var cacheCurrentPlayList = JSON.parse(
        window.localStorage.getItem("currentPlayList")
      );
      var i =
        this.findCurrentSongidx(id) == 0
          ? cacheCurrentPlayList.length - 1
          : this.findCurrentSongidx(id) - 1;
      this.songInfo = cacheCurrentPlayList[i];
    },

    //随机播放
    randomSong(index) {
      var cacheCurrentPlayList = JSON.parse(
        window.localStorage.getItem("currentPlayList")
      );
      this.songInfo = cacheCurrentPlayList[index];
    }
  }
};
</script>
<style lang="less">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
</style>
